import React, { useState, useRef, useEffect, useCallback } from "react";
import {
  Col,
  Form,
  Image,
  Row,
  Stack,
  Overlay,
  Popover,
  Button,
} from "react-bootstrap";
// import Button from '@mui/material/Button';
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";

import { TileMap, TileMapProps, renderMap } from "react-tile-map";
import { Popup } from "decentraland-ui";
import "./Styles.css";
export const COLOR_BY_TYPE = Object.freeze({
  0: "#ff9990", // my parcels
  1: "#ff4053", // my parcels on sale
  2: "#ff9990", // my estates
  3: "#ff4053", // my estates on sale
  4: "#ffbd33", // parcels/estates where I have permissions
  5: "#5054D4", // districts
  6: "#563db8", // contributions
  7: "#716C7A", // roads
  8: "#70AC76", // plazas
  9: "#3D3A46", // owned parcel/estate
  10: "#3D3A46", // parcels on sale (we show them as owned parcels)
  11: "#09080A", // unowned pacel/estate
  12: "#18141a", // background
  13: "#110e13", // loading odd
  14: "#0d0b0e", // loading even

  15: "#00eeff",
});
function Atlas({ setSelectedAxis, parcels }) {
  const [showPopup, setShowPopup] = useState(false);
  const [hoveredTile, setHoveredTile] = useState({
    type: 9,
    x: 16,
    y: -11,
    owner: "0xff6bbfbd72551f02be3109ad6eec9ed469bf7667",
  });
  const [mouseX, setMouseX] = useState(-1);
  const [mouseY, setMouseY] = useState(-1);
  const [x, setX] = useState(0);
  const [y, setY] = useState(0);
  const getCoords = (x, y) => `${x},${y}`;

  // const [parcels, setParcels] = useState();
  // const getdata = async () => {
  //   const res = await fetch(link);
  //   const json = await res.json();
  //   if (json.ok) {
  //     // const sliced = Object.fromEntries(
  //     //   Object.entries(json.data).slice(0, 5)
  //     // )
  //     // console.log("ZXSDSSAsa",sliced)
  //     setParcels(json.data);
  //   }
  // };

  // useEffect(() => {
  //   getdata();
  // }, []);
  const handleHidePopup = useCallback(() => {
    setShowPopup(false);
    setMouseX(-1);
    setMouseY(-1);
  }, []);
  const handleHover = useCallback(
    (x, y) => {
      setX(x);
      setY(y);
      const id = getCoords(x, y);
      const tile = parcels[id];
      console.log("sddssdfds", tile);
      if (tile && !showPopup) {
        setShowPopup(true);
        setHoveredTile(tile);
        setMouseX(-1);
        setMouseY(-1);
      } else if (tile && tile !== hoveredTile) {
        setHoveredTile(tile);
        setMouseX(-1);
        setMouseY(-1);
      } else if (!tile && showPopup) {
        setShowPopup(false);
      }
    },
    [hoveredTile, showPopup, parcels]
  );
  const getType = useCallback((x, y) => {
    const id = getCoords(x, y);
    const tile = parcels[id];
    if (tile.type == 9) {
      return true;
    } else {
      return false;
    }
  }, []);
  const simpleLayer = (x, y) => {
    return {
      color: "#cccccc",
    };
  };
  const chessboardLayer = (x, y) => {
    return {
      color: (x + y) % 2 === 0 ? "#cccccc" : "#888888",
    };
  };
  const [selected, setSelected] = useState([]);

  const isSelected = (x, y) => {
    return selected.some((coord) => coord.x === x && coord.y === y);
  };
  const selectedStrokeLayer = (x, y) => {
    return isSelected(x, y) ? { color: "#00ff00", scale: 1.4 } : null;
  };

  const selectedFillLayer = (x, y) => {
    return isSelected(x, y) ? { color: "#00dd00", scale: 1.2 } : null;
  };
  let hover = { x: 0, y: 0 };

  const isPositive = (x, y) => x > 0 && y > 0;

  const positiveLayer = (x, y) => {
    return {
      color: isPositive(x, y) ? "#cccccc" : "#888888",
    };
  };

  const hoverLayer = (x, y) => {
    return hover.x === x && hover.y === y
      ? { color: isPositive(x, y) ? "#ff0000" : "#00ff00" }
      : null;
  };
  const clickedLayer = (x, y) => {
    return {
      color: isSelected(x, y) ? "#00ff00" : "#888888",
    };
  };

  const connectedLayer = (x, y) => {
    const top = x % 10 === 0;
    const left = y % 10 === 0;
    return {
      color: top || left ? "#888888" : "#cccccc",
      top,
      left,
    };
  };
  let atlas = parcels;
  const atlasLayer = (x, y) => {
    const id = x + "," + y;
    if (atlas !== null && id in atlas) {
      const tile = atlas[id];
      const color = COLOR_BY_TYPE[tile.type];

      const top = !!tile.top;
      const left = !!tile.left;
      const topLeft = !!tile.topLeft;

      return {
        color,
        top,
        left,
        topLeft,
      };
    } else {
      return {
        color: (x + y) % 2 === 0 ? COLOR_BY_TYPE[12] : COLOR_BY_TYPE[13],
      };
    }
  };

  const onSaleLayer = (x, y) => {
    const id = x + "," + y;
    if (atlas && id in atlas && atlas[id].price) {
      const color = "#00d3ff";
      const top = !!atlas[id].top;
      const left = !!atlas[id].left;
      const topLeft = !!atlas[id].topLeft;
      return {
        color,
        top,
        left,
        topLeft,
      };
    }
    return null;
  };
  const checkType = (x, y) => {
    // console.log("ASdadasd", x, y);
  };
  const hoverfunction = (x, y) => {
    checkType(x, y);
    setShowPopup(true);
  };
  const longText = `
  Aliquam eget finibus ante, non facilisis lectus. Sed vitae dignissim est, vel aliquam tellus.
  Praesent non nunc mollis, fermentum neque at, semper arcu.
  Nullam eget est sed sem iaculis gravida eget vitae justo.
  `;

  const getPostionOfHover = (x) => {
    if (x >= 100 && x <= 150) {
      return 100;
    } else if (x >= 50 && x <= 100) {
      return 150;
    } else if (x >= 0 && x <= 50) {
      return 200;
    } else if (x >= -50 && x <= 0) {
      return 250;
    } else if (x >= -100 && x <= -50) {
      return 300;
    } else if (x >= -150 && x <= -100) {
      return 350;
    } else {
      return 400;
    }
  };
  return (
    <div
      style={{ height: 500, backgroundColor: "black" }}
      onMouseLeave={handleHidePopup}
    >
      <TileMap
        className="map"
        height={500}
        width={600}
        //  layers={[simpleLayer]}
        //  layers={[chessboardLayer]}
        //  layers={[chessboardLayer, selectedStrokeLayer, selectedFillLayer]}
        // layers={[positiveLayer, hoverLayer]}
        // onHover={(x, y) => (hover = { x, y })}
        // layers={[clickedLayer]}
        // onClick={(x, y) => selected.push({ x, y })}
        // layers={[connectedLayer]}
        // layers={[atlasLayer]} Math.abs(x)
        layers={[
          atlasLayer,
          onSaleLayer,
          selectedStrokeLayer,
          selectedFillLayer,
        ]}
        onClick={(x, y) => {
          if (!getType(x, y)) {
            return;
          }
          let removedArr = [];
          let alreadySelected = selected.filter((item) => {
            if (item.x == x && item.y == y) {
              return item;
            } else {
              removedArr.push(item);
            }
          });
          let newArr = [...selected];
          if (alreadySelected.length == 0) {
            newArr.push({ x, y });
            setSelected(newArr);
            setSelectedAxis(newArr);
          } else {
            setSelected(removedArr);
            setSelectedAxis(removedArr);
          }

          // setSelected([{ x, y }]);
          // setSelectedAxis([{ x, y }]);
        }}
        onHover={handleHover}
        zoom={0.5}
        x={0}
        y={0}
      />
      {showPopup ? (
        <div
          className="tip"
          style={{
            top: y > 0 ? y + 100 : y + 200,
            left: x > 0 ? x + 100 : x + 300,
          }}
        >
          {hoveredTile.type == 8
            ? "Place: " +
              "public place" +
              "\n" +
              "x:" +
              hoveredTile.x +
              "\n" +
              "y:" +
              hoveredTile.y
            : hoveredTile.type == 7
            ? "Place: Road" +
              "" +
              "\n" +
              "x:" +
              hoveredTile.x +
              "\n" +
              "y:" +
              hoveredTile.y
            : hoveredTile.type == 5
            ? "Place: Sea" +
              "" +
              "\n" +
              "x:" +
              hoveredTile.x +
              "\n" +
              "y:" +
              hoveredTile.y
            : "Owner:" +
              hoveredTile.owner +
              "\n" +
              "x:" +
              hoveredTile.x +
              "\n" +
              "y:" +
              hoveredTile.y}
          {hoveredTile?.name &&"\n"+ "Name:" + hoveredTile?.name}
          {/* <Popover
            x={x}
            y={y}
            visible={true}
            tile={hoveredTile}
            position={x > window.innerWidth - 280 ? "left" : "right"}
          ></Popover> */}
        </div>
      ) : null}
    </div>
  );
}

export default Atlas;
