// constants
import Web3 from "web3";
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import store from "./store";
import Landparcels from "../contracts/Landparcels.json";
import NftContract from "../contracts/Nftcontract.json";
import { postMethod } from "../helpers/API&Helpers";
const connectRequest = () => {
  return {
    type: "CONNECTION_REQUEST",
  };
};

export const connectSuccess = (payload) => {
  return {
    type: "CONNECTION_SUCCESS",
    payload: payload,
  };
};

export const connectFailed = (payload) => {
  return {
    type: "CONNECTION_FAILED",
    payload: payload,
  };
};
const checkUserPresence = async (address) => {
  if (address != "") {
    let url = "signup";
    let params = {
      wallet: address,
    };
    let authtoken = "";
    let response = await postMethod({ url, params, authtoken });
  }
};
