const getProviderOptions = () => {
  const providerOptions = {
    walletconnect: {
      package: WalletConnectProvider,
      options: {
        //infuraId: process.env.REACT_APP_INFURA_ID
        rpc: {
          51: "https://rpc.apothem.network",
          4: "https://rinkeby.infura.io/v3/",
        },
      },
    },
  };

  return providerOptions;
};

export const connectWallet = (walletname) => {
  return async (dispatch) => {
    dispatch(connectRequest());
    try {
      const web3Modal = new Web3Modal({
        cacheProvider: true,
        providerOptions: getProviderOptions(), // required
      });

      // const provider = await web3Modal.connect();

      var provider = "";
      if (walletname === "coinbasewallet") {
        var provider = await web3Modal.connectTo("coinbasewallet");
      } else if (walletname === "walletconnect") {
        var provider = await web3Modal.connectTo("walletconnect");
      } else if (walletname === "fortmatic") {
        var provider = await web3Modal.connectTo("fortmatic");
      } else if (walletname === "metamask") {
        const web3Modal = new Web3Modal({
          cacheProvider: true,
          providerOptions: getProviderOptions().walletconnect, // required
        });
        var provider = await web3Modal.connect();
      } else {
        const web3Modal = new Web3Modal({
          cacheProvider: true,
          providerOptions: getProviderOptions().walletconnect, // required
        });
        var provider = await web3Modal.connect();
      }

      // const tokenContractAddress = '';

      await subscribeProvider(provider);

      const web3 = new Web3(provider);

      const accounts = await web3.eth.getAccounts();
      const address = accounts[0];
      // const user =   checkUserPresence(address)
      console.log("NftSing test1",address); 
      const Land = new web3.eth.Contract(
        Landparcels,
        process.env.REACT_APP_LAND_CONTRACT
      );   
        console.log("NftSing test22222",); 
      const NftSingle = new web3.eth.Contract(
        NftContract,
        process.env.REACT_APP_NFT_CONTRACT
      );
      console.log("NftSingle");  
      if (window.ethereum && window.ethereum.networkVersion !== "51") {
        await addNetwork(51);
      }

      dispatch(
        connectSuccess({
          address,
          web3,
          provider,
          connected: true,
          web3Modal,
          Land,
          NftSingle
        })
      );
    } catch (e) {
      dispatch(connectFailed(e));
    }
  };
};

const subscribeProvider = async (provider) => {
  if (!provider.on) {
    return;
  }

  provider.on("connect", async (id) => {
    // console.log(id);
  });

  provider.on("networkChanged", async (networkId) => {
    if (networkId !== "51") {
      // console.log(networkId);
      store.dispatch(connectFailed("Please switch to Binance mainnet"));
    } else {
      store.dispatch(connectWallet());
    }
  });
};

export async function addNetwork(id) {
  let networkData;
  switch (id) {
    //bsctestnet
    case 4:
      networkData = [
        {
          chainId: "0x4",
        },
      ];

      break;
    case 51:
      networkData = [
        {
          chainId: "0x33",
          chainName: "XinFin Apothem Testnet",
          rpcUrls: ["https://rpc.apothem.network"],
          nativeCurrency: {
            name: "XinFin Apothem Testnet",
            symbol: "TXDC",
            decimals: 18,
          },
          blockExplorerUrls: ["https://xinfin.org"],
        },
      ];

      break;
    //bscmainet
    case 56:
      networkData = [
        {
          chainId: "0x38",
          chainName: "BSCMAINET",
          rpcUrls: ["https://bsc-dataseed1.binance.org"],
          nativeCurrency: {
            name: "BINANCE COIN",
            symbol: "BNB",
            decimals: 18,
          },
          blockExplorerUrls: ["https://testnet.bscscan.com/"],
        },
      ];
      break;
    default:
      break;
  }
  if (Number(id) != 51) {
    return window.ethereum.request({
      method: "wallet_addEthereumChain",
      params: networkData,
    });
  } else {
    return window.ethereum.request({
      method: "wallet_switchEthereumChain",
      params: networkData,
    });
  }
  return window.ethereum.request({
    method: "wallet_addEthereumChain",
    params: networkData,
  });
}

(() => {
  if (window.ethereum) {
    window.ethereum.on("networkChanged", function (networkId) {
      if (networkId !== "56") {
        // console.log(networkId);
        store.dispatch(connectFailed("Please switch to Binance mainnet"));
      } else {
        store.dispatch(connectWallet());
      }
    });
  }
})();
